import { useWallet } from 'use-wallet';
import { Badge, Fade } from "shards-react";
import useTokenBalance from "../../../hooks/useTokenBalance";
import { displayAmount } from "../../../world/utils";
import useTokenPrice from "../../../hooks/useTokenPrice";
import Value from "../../common/Value";
import { useState } from "react";

export default () => {
  const [ethPrice, setEthPrice] = useState(false);
  const {account} = useWallet();
  const balance = useTokenBalance();
  const tokenPrice = useTokenPrice();

  return (
    <div className="main-navbar__search w-100 d-none d-md-flex d-lg-flex align-items-center">
      <div className="ml-4 d-flex">
        <Badge
          href="#"
          theme="success"
          className="mr-2"
          onClick={() => {
            setEthPrice(!ethPrice);
          }}>
          1 WORLD = <Value value={ethPrice ? tokenPrice.eth : tokenPrice.usd} decimals={4}/> {ethPrice ? 'ETH' : 'USD'}
        </Badge>
        <Fade in={account}>
          <Badge theme="info" className="mr-2">{displayAmount(balance)} WORLD</Badge>
          <Badge theme="info" outline>{account}</Badge>
        </Fade>
      </div>
    </div>
  );
}
