import { useEffect, useState } from 'react'

import useBlock from './useBlock'
import useWorldFarm from "./useWorldFarm";
import { BigNumber } from "ethers";
import WorldFarm from "../world/WorldFarm";

const useWorldPerBlock = () => {
  const [worldPerBlock, setWorldPerBlock] = useState(BigNumber.from(0));
  const worldFarm = useWorldFarm();
  const block = useBlock();

  const fetchWorldPerBlock = async () => {
    const worldPerBlock = await WorldFarm.read.getWorldPerBlock();
    setWorldPerBlock(worldPerBlock);
  };

  useEffect(() => {
    fetchWorldPerBlock();
  }, [block, worldFarm]);

  return worldPerBlock;
}

export default useWorldPerBlock;
