import { useState } from "react";
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  FormInput,
  InputGroup,
  InputGroupAddon,
  ListGroup,
  ListGroupItem,
  ListGroupItemHeading,
  Row
} from "shards-react";
import useWorldFarm from "../../hooks/useWorldFarm";
import { displayAmount, shortenAddress } from "../../world/utils";
import useLpTokenBalance from "../../hooks/useLpTokenBalance";
import { BigNumber, utils } from "ethers";
import useLpTokenAllowance from "../../hooks/useLpTokenAllowance";
import { useWallet } from 'use-wallet';
import { BLOCKS_PER_DAY, CHAIN_ID, CONTRACT_ADDRESSES } from "../../world/constants";
import Value from "../common/Value";
import useStakedBalance from "../../hooks/useStakedBalance";
import useFarmROI from "../../hooks/useFarmROI";
import useTokenPrice from "../../hooks/useTokenPrice";

const Stake = () => {
  const {account} = useWallet();
  const [amount, setAmount] = useState(0);
  const {setAllowance, allowance} = useLpTokenAllowance();
  const worldFarm = useWorldFarm();
  const balance = useLpTokenBalance();
  const stakingAllowed = !allowance.isZero();

  const {totalStakedLP, worldPerBlock} = useFarmROI();
  const tokenPrice = useTokenPrice();
  const worldPerDay = !totalStakedLP.isZero() && amount
    ? ((amount * 1e18) / totalStakedLP.toString()) * worldPerBlock * BLOCKS_PER_DAY
    : 0;

  return (
    <Card small className="blog-comments">
      <CardHeader className="border-bottom">
        <h6 className="m-0">Stake WORLD-ETH LP Tokens</h6>
      </CardHeader>

      <CardBody className="d-flex flex-column">
        <label htmlFor="lpAmount">Enter LP amount to stake</label>
        <InputGroup>
          <FormInput
            id="lpAmount"
            placeholder="0"
            onChange={(e) => {
              const num = +e.target.value;
              setAmount(num ? num : 0);
            }}
            value={amount}
          />
          <InputGroupAddon type="append">
            <Button
              theme="white"
              onClick={() => {
                if (!balance.isZero()){
                  setAmount(utils.formatEther(balance));
                }
              }}>
              Max
            </Button>
          </InputGroupAddon>
          <InputGroupAddon type="append">
            <Button
              theme={stakingAllowed ? 'accent' : 'info'}
              onClick={async () => {
                if (stakingAllowed) {
                  try {
                    await worldFarm?.stake(amount);
                    setAmount(0);
                  } catch (e) {
                    console.log('Stake error: ', e);
                    setAllowance(BigNumber.from(0));
                  }
                  return;
                }

                try {
                  const amount = await worldFarm?.lpTokenMaxApprove();
                  setAllowance(amount);
                } catch (e) {
                  console.log('Approve error: ', e);
                }
              }}
            >
              {stakingAllowed ? 'Stake' : 'Approve'}
            </Button>
          </InputGroupAddon>
        </InputGroup>

        <ListGroup flush className="list-group-small">
          <ListGroupItemHeading/>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue"><b>Available balance</b></span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <b><Value value={displayAmount(balance)}/></b>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">WORLD per day</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <Value value={displayAmount(worldPerDay)}/>
            </span>
          </ListGroupItem>
          {/*<ListGroupItem className="d-flex px-3">*/}
          {/*  <span className="text-semibold text-fiord-blue">WORLD per week</span>*/}
          {/*  <span className="ml-auto text-right text-semibold text-reagent-gray">*/}
          {/*    123*/}
          {/*  </span>*/}
          {/*</ListGroupItem>*/}
          {/*<ListGroupItem className="d-flex px-3">*/}
          {/*  <span className="text-semibold text-fiord-blue">WORLD per month</span>*/}
          {/*  <span className="ml-auto text-right text-semibold text-reagent-gray">*/}
          {/*    123*/}
          {/*  </span>*/}
          {/*</ListGroupItem>*/}
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">Earnings per day (USD)</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              $<Value value={displayAmount(worldPerDay * tokenPrice.usd)} decimals={4}/>
            </span>
          </ListGroupItem>
          {/*<ListGroupItem className="d-flex px-3">*/}
          {/*  <span className="text-semibold text-fiord-blue">Earnings per week (USD)</span>*/}
          {/*  <span className="ml-auto text-right text-semibold text-reagent-gray">*/}
          {/*    $123*/}
          {/*  </span>*/}
          {/*</ListGroupItem>*/}
          {/*<ListGroupItem className="d-flex px-3">*/}
          {/*  <span className="text-semibold text-fiord-blue">Earnings per month (USD)</span>*/}
          {/*  <span className="ml-auto text-right text-semibold text-reagent-gray">*/}
          {/*    $123*/}
          {/*  </span>*/}
          {/*</ListGroupItem>*/}
        </ListGroup>
      </CardBody>

      <CardFooter className="border-top">
        <Row>
          <Col className="view-report d-flex">
            <Badge theme="info" outline>
              1% of transaction tax goes to the reward pool
            </Badge>
          </Col>
          <Col className="view-report d-flex justify-content-end">
            <Badge href={`https://etherscan.com/address/${CONTRACT_ADDRESSES.worldFarm[CHAIN_ID]}`} theme="light">
              Contract: <span className="text-break">{shortenAddress(CONTRACT_ADDRESSES.worldFarm[CHAIN_ID], 7)}</span>
            </Badge>
          </Col>
        </Row>
      </CardFooter>
    </Card>
  );
};

export default Stake;
