import { Card, CardBody, CardHeader, ListGroup, ListGroupItem } from "shards-react";
import { displayAmount, ZERO_BN } from "../../world/utils";
import useStakedBalance from "../../hooks/useStakedBalance";
import useFarmROI from "../../hooks/useFarmROI";
import { BLOCKS_PER_DAY } from "../../world/constants";
import useTokenPrice from "../../hooks/useTokenPrice";
import { utils } from 'ethers';
import Value from "../common/Value";

const StakingInfo = () => {
  const stakedBalance = useStakedBalance();
  const {totalStakedLP, worldPerBlock} = useFarmROI();
  const tokenPrice = useTokenPrice();

  const worldPerDay = !totalStakedLP.isZero() && !stakedBalance.isZero()
    ? (stakedBalance.toString() / totalStakedLP.toString()) * worldPerBlock * BLOCKS_PER_DAY
    : 0;

  return (
    <Card small className="blog-comments">
      <CardHeader className="border-bottom">
        <h6 className="m-0">Staking Information</h6>
      </CardHeader>

      <CardBody className="d-flex flex-column">
        <ListGroup flush className="list-group-small">
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue"><b>WORLD per day</b></span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <b><Value value={displayAmount(worldPerDay)}/></b>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">WORLD per week</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <Value value={displayAmount(worldPerDay * 7)}/>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">WORLD per month</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <Value value={displayAmount(worldPerDay * 31)}/>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">Earnings per day (USD)</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              $<Value value={displayAmount(worldPerDay * tokenPrice.usd)} decimals={4}/>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">Earnings per week (USD)</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              $<Value value={displayAmount((worldPerDay * 7) * tokenPrice.usd)} decimals={4}/>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">Earnings per month (USD)</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              $<Value value={displayAmount((worldPerDay * 31) * tokenPrice.usd)} decimals={4}/>
            </span>
          </ListGroupItem>
        </ListGroup>
      </CardBody>
    </Card>
  );
};

export default StakingInfo;
