import { Button, Card, CardBody, CardFooter, CardHeader, Col, ListGroup, ListGroupItem, Row } from "shards-react";
import { displayAmount } from "../../world/utils";
import useStakedBalance from "../../hooks/useStakedBalance";
import usePendingRewards from "../../hooks/usePendingRewards";
import Value from "../common/Value";
import useTokenPrice from "../../hooks/useTokenPrice";
import { utils } from 'ethers';

const StakingInfo = () => {
  const stakedBalance = useStakedBalance();
  const pendingRewards = usePendingRewards() || '0';
  const tokenPrice = useTokenPrice();

  return (
    <Card small className="blog-comments">
      <CardHeader className="border-bottom">
        <h6 className="m-0">Staking Actions</h6>
      </CardHeader>

      <CardBody className="d-flex flex-column">
        <ListGroup flush className="list-group-small">
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue"><b>Pending rewards</b></span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <b><Value value={displayAmount(pendingRewards)}/></b>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">Current earnings (USD)</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              $<Value value={displayAmount(utils.formatEther(pendingRewards) * tokenPrice.usd)} decimals={4}/>
            </span>
          </ListGroupItem>
          <ListGroupItem className="d-flex px-3">
            <span className="text-semibold text-fiord-blue">Staked balance</span>
            <span className="ml-auto text-right text-semibold text-reagent-gray">
              <Value value={displayAmount(stakedBalance)}/>
            </span>
          </ListGroupItem>
        </ListGroup>
      </CardBody>

      <CardFooter className="border-top">
        <Row>
          <Col className="view-report d-flex justify-content-between">
            <Button theme="success">
              Claim rewards
            </Button>
            <Button theme="secondary">
              Unstake
            </Button>
          </Col>
        </Row>
      </CardFooter>
    </Card>
  );
};

export default StakingInfo;
