import "./shards-styles/shards-dashboards.1.1.0.min.css";
import './App.css';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import { UseWalletProvider } from 'use-wallet';
import Analytics from 'react-router-ga';
import { CHAIN_ID, RINKEBY_CHAIN_ID, RINKEBY_RPC_URL, RPC_URL, } from './world/constants';
import WorldTokenContext from './contexts/WorldTokenContext';
import DefaultLayout from "./components/layout/DefaultLayout";
import Farm from "./views/Farm";
import NotFound from "./views/NotFound";
import Overview from "./views/Overview";
import WorldFarmContext from "./contexts/WorldFarmContext";

const Providers = ({children}) => (
  <UseWalletProvider
    chainId={CHAIN_ID}
    connectors={{
      walletconnect: {rpcUrl: RPC_URL},
    }}
  >
    <WorldTokenContext>
      {children}
    </WorldTokenContext>
  </UseWalletProvider>
);

function App() {
  return (
    <Providers>
      <BrowserRouter>
        <Analytics id="UA-185956664-1">
          <DefaultLayout>
            <Switch>
              <Route path="/" exact>
                <Redirect to="/farm"/>
              </Route>
              {/*<Route path="/overview">*/}
              {/*  <Overview/>*/}
              {/*</Route>*/}
              <Route path="/farm">
                <Farm/>
              </Route>
              <Route path="*">
                <NotFound/>
              </Route>
            </Switch>
          </DefaultLayout>
        </Analytics>
      </BrowserRouter>
    </Providers>
  );
}

export default App;
