import approx from 'approximate-number';
import { BigNumber, utils } from 'ethers';

export const ZERO_BN = BigNumber.from(0);

export const displayAmount = (amount) => {
  if (!isNaN(amount) && amount !== Infinity) {
    try {
      amount = utils.formatEther(amount);
    } catch (e) {
    }
    return +toFixedString(amount, 3);
  }

  return '0';
}

function toFixedString(num, fixed) {
  const re = new RegExp('^-?\\d+(?:\.\\d{0,' + (fixed || -1) + '})?');
  return num.toString().match(re)[0];
}

export const formatTime = (time) => {
  if (!time) {
    return '';
  }
  return `${time.format('MMM D, YYYY').toString()} @ ${time.format('hh:mm A').toString()} (UTC${time.format('Z').toString()})`;
};

